'use client'

import { useState, useEffect } from 'react'
import { useAuth } from '@/contexts/AuthContext' // CORREGIDO: cambiar de @/hooks/useAuth a @/contexts/AuthContext
import { getMisViaticos } from '@/services/api'
import { format } from 'date-fns'
import { es } from 'date-fns/locale'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Badge } from '@/components/ui/badge'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip'
import Link from 'next/link'
import { RefreshCw, Eye, Receipt, Loader2, FileText, DollarSign, Grid3x3, Table2, TrendingUp, ArrowRight } from 'lucide-react'
import Layout from '@/components/Layout'
import AuthGuard from '@/components/AuthGuard'

interface Viatico {
  id: string
  fecha: string
  descripcion: string
  tipo: string
  monto: number | string
  url_onedrive?: string
}

export default function MisViaticosPage() {
  const { user, loading: authLoading } = useAuth() // Usar loading tambi칠n
  const [viaticos, setViaticos] = useState<Viatico[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const [viewMode, setViewMode] = useState<'grid' | 'table'>('grid')

  useEffect(() => {
    // Solo cargar si el usuario est치 autenticado y no est치 cargando
    if (user && !authLoading) {
      loadViaticos()
    }
  }, [user, authLoading])

  const loadViaticos = async () => {
    // No cargar si a칰n est치 verificando autenticaci칩n
    if (authLoading) {
      return
    }
    
    // Si no hay usuario, AuthGuard se encargar치 de redirigir
    if (!user) {
      setLoading(false)
      return
    }
    
    try {
      setLoading(true)
      setError('')
      
      const data = await getMisViaticos()
      setViaticos(data.viaticos || [])
    } catch (err) {
      const errorMessage = (err as Error).message || 'Error al cargar los vi치ticos'
      
      // Si el error es de autenticaci칩n, mostrar mensaje m치s claro
      if (errorMessage.includes('no autenticado') || errorMessage.includes('No autorizado') || errorMessage.includes('Sesi칩n expirada')) {
        setError('Sesi칩n expirada. Por favor, inicia sesi칩n nuevamente.')
      } else {
        setError(errorMessage)
      }
    } finally {
      setLoading(false)
    }
  }

  const total = viaticos.reduce((sum, v) => sum + parseFloat(String(v.monto || 0)), 0)
  const totalCount = viaticos.length

  const groupedByMonth = viaticos.reduce((acc, v) => {
    const monthKey = format(new Date(v.fecha), 'MMMM yyyy', { locale: es })
    if (!acc[monthKey]) {
      acc[monthKey] = []
    }
    acc[monthKey].push(v)
    return acc
  }, {} as Record<string, Viatico[]>)

  // Mostrar loading mientras se verifica autenticaci칩n
  if (authLoading) {
    return (
      <Layout>
        <div className="flex items-center justify-center min-h-[400px]">
          <div className="text-center space-y-4">
            <Loader2 className="h-8 w-8 animate-spin text-primary mx-auto" />
            <p className="text-muted-foreground">Verificando autenticaci칩n...</p>
          </div>
        </div>
      </Layout>
    )
  }

  if (loading && viaticos.length === 0) {
    return (
      <Layout>
        <div className="flex items-center justify-center min-h-[400px]">
          <div className="text-center space-y-4">
            <Loader2 className="h-8 w-8 animate-spin text-primary mx-auto" />
            <p className="text-muted-foreground">Cargando tus vi치ticos...</p>
          </div>
        </div>
      </Layout>
    )
  }

  return (
    <AuthGuard>
      <Layout>
      <TooltipProvider>
        <div className="space-y-6">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold tracking-tight">Mis Vi치ticos</h1>
              <p className="text-muted-foreground">
                Consulta y gestiona todos tus registros
              </p>
            </div>
            <div className="flex items-center gap-2">
              <Tooltip>
                <TooltipTrigger asChild>
                  <Button 
                    variant="outline" 
                    size="icon"
                    onClick={() => setViewMode(viewMode === 'grid' ? 'table' : 'grid')}
                  >
                    {viewMode === 'grid' ? <Table2 className="h-4 w-4" /> : <Grid3x3 className="h-4 w-4" />}
                  </Button>
                </TooltipTrigger>
                <TooltipContent>
                  <p>Cambiar vista</p>
                </TooltipContent>
              </Tooltip>
              <Button 
                variant="outline" 
                size="icon"
                onClick={loadViaticos} 
                disabled={loading}
              >
                <RefreshCw className={`h-4 w-4 ${loading ? 'animate-spin' : ''}`} />
              </Button>
            </div>
          </div>

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <div className="grid gap-4 md:grid-cols-3">
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Total de Vi치ticos</CardTitle>
                <Receipt className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{totalCount}</div>
                <p className="text-xs text-muted-foreground">Registros totales</p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Total Gastado</CardTitle>
                <DollarSign className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">S/ {total.toFixed(2)}</div>
                <p className="text-xs text-muted-foreground">Suma total</p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Promedio</CardTitle>
                <TrendingUp className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">
                  {totalCount > 0 ? `S/ ${(total / totalCount).toFixed(2)}` : 'S/ 0.00'}
                </div>
                <p className="text-xs text-muted-foreground">Por vi치tico</p>
              </CardContent>
            </Card>
          </div>

          {viaticos.length === 0 ? (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12">
                <FileText className="h-12 w-12 text-muted-foreground mb-4" />
                <h3 className="text-lg font-semibold mb-2">No tienes vi치ticos registrados</h3>
                <p className="text-sm text-muted-foreground text-center mb-4">
                  Comienza registrando tu primer vi치tico
                </p>
                <Button asChild>
                  <Link href="/nuevo-viatico">
                    Crear Primer Vi치tico
                    <ArrowRight className="ml-2 h-4 w-4" />
                  </Link>
                </Button>
              </CardContent>
            </Card>
          ) : viewMode === 'table' ? (
            <Card>
              <CardHeader>
                <CardTitle>Lista de Vi치ticos</CardTitle>
                <CardDescription>Vista detallada de todos tus vi치ticos</CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Fecha</TableHead>
                      <TableHead>Descripci칩n</TableHead>
                      <TableHead>Tipo</TableHead>
                      <TableHead className="text-right">Monto</TableHead>
                      <TableHead className="text-right">Acciones</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {viaticos.map((viatico) => (
                      <TableRow key={viatico.id}>
                        <TableCell className="font-medium">
                          {format(new Date(viatico.fecha), 'dd MMM yyyy', { locale: es })}
                        </TableCell>
                        <TableCell>
                          <Tooltip>
                            <TooltipTrigger asChild>
                              <span className="truncate block max-w-[300px]">{viatico.descripcion}</span>
                            </TooltipTrigger>
                            <TooltipContent>
                              <p className="max-w-xs">{viatico.descripcion}</p>
                            </TooltipContent>
                          </Tooltip>
                        </TableCell>
                        <TableCell>
                          <Badge variant="outline">{viatico.tipo || 'otro'}</Badge>
                        </TableCell>
                        <TableCell className="text-right font-medium">
                          S/ {parseFloat(String(viatico.monto)).toFixed(2)}
                        </TableCell>
                        <TableCell className="text-right">
                          {viatico.url_onedrive && (
                            <Button variant="outline" size="sm" asChild>
                              <a 
                                href={viatico.url_onedrive} 
                                target="_blank" 
                                rel="noopener noreferrer"
                              >
                                <Eye className="h-4 w-4 mr-2" />
                                Ver
                              </a>
                            </Button>
                          )}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          ) : (
            <div className="space-y-6">
              {Object.entries(groupedByMonth).map(([month, monthViaticos]) => {
                const monthTotal = monthViaticos.reduce((sum, v) => sum + parseFloat(String(v.monto || 0)), 0)
                return (
                  <Card key={month}>
                    <CardHeader>
                      <CardTitle className="capitalize">{month}</CardTitle>
                      <CardDescription>
                        {monthViaticos.length} vi치tico{monthViaticos.length !== 1 ? 's' : ''}  Total: S/ {monthTotal.toFixed(2)}
                      </CardDescription>
                    </CardHeader>
                    <CardContent>
                      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                        {monthViaticos.map((viatico) => (
                          <Card key={viatico.id}>
                            <CardHeader>
                              <CardTitle className="text-base">{viatico.descripcion}</CardTitle>
                              <CardDescription>
                                <Badge variant="outline" className="mr-2">{viatico.tipo || 'otro'}</Badge>
                                {format(new Date(viatico.fecha), 'dd MMM', { locale: es })}
                              </CardDescription>
                            </CardHeader>
                            <CardContent>
                              <div className="flex items-center justify-between">
                                <div className="text-2xl font-bold">S/ {parseFloat(String(viatico.monto)).toFixed(2)}</div>
                                {viatico.url_onedrive && (
                                  <Button variant="outline" size="sm" asChild>
                                    <a 
                                      href={viatico.url_onedrive} 
                                      target="_blank" 
                                      rel="noopener noreferrer"
                                    >
                                      <Eye className="h-4 w-4" />
                                    </a>
                                  </Button>
                                )}
                              </div>
                            </CardContent>
                          </Card>
                        ))}
                      </div>
                    </CardContent>
                  </Card>
                )
              })}
            </div>
          )}
        </div>
      </TooltipProvider>
    </Layout>
    </AuthGuard>
  )
}

